<template>
  <div v-if="comments.length" class="subcomments">
    <SubCommentItem
      v-for="(item, index) in commentsList"
      :key="index"
      :comment="item"
    />

    <LoadMore :loading="loading" :has-next="hasNext" @more="loadSubComments" />
  </div>
</template>

<script lang="ts">
export default {
  name: "SubComments",
};
</script>

<script lang="ts" setup>
import { onMounted, ref, watch, computed } from "vue";
import { getSubComments } from "../services";
import { useGlobals } from "../composables";
import SubCommentItem from "./SubCommentItem.vue";
import LoadMore from "./LoadMore.vue";

const props = defineProps({
  id: { type: [String, Number], default: "" },
});

const globals = useGlobals();

const comments = ref<any[]>([]);
const hasNext = ref(true);
const loading = ref(false);

const commentsList = computed(() => [
  ...globals.topSubComments.value,
  ...comments.value,
]);

onMounted(() => {
  loadSubComments(true);
});

watch(
  () => [globals.sort],
  () => loadSubComments(true)
);

async function loadSubComments(reload = false) {
  if (loading.value) return;

  if (reload) {
    globals.topSubComments.value = [];
    comments.value = [];
    hasNext.value = true;
  }

  loading.value = true;

  try {
    const resp = await getSubComments({
      limit: globals.limit,
      comment_id: "" + props.id,
      offset: comments.value.length,
    });

    hasNext.value = resp.replies.length >= globals.limit;
    comments.value = [
      ...comments.value,
      ...resp.replies.filter(
        (x) => !globals.topSubComments.value.find((y) => y.id === x.id)
      ),
    ];
  } catch (error) {
    console.error("Get Sub Comments Error", error);
  }

  loading.value = false;
}
</script>

<style lang="scss" scoped>
.subcomments {
  background: rgb(var(--v-theme-greyscale_6));
  padding: 8px;
  border-radius: 8px;
  margin-top: 16px;
}
</style>
